.heading-part-technology {
  text-align: center;
  margin: auto;

  .heading-part {
    margin-bottom: 50px;
    display: inline-block;
    position: relative;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
  }
}

.technology-imp {
  text-align: center;
  margin: auto;
}

/*  TechnologyList  */
.technology-list-container {
  padding-top: 50px;

  & ul {
    & li {
      margin-bottom: 30px;
    }
  }
}

p.project-name a {
  color: #46007b;
  font-weight: 600;
  font-size: 19px;
}

p.project-technology {
  color: #1f1f1f;
}

p.project-additionally {
  color: #46007b;

  & img {
    width: 24px;
  }
}

p.git-hub-link {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 40ch;
}


.arrow-close.technology-back {
  & img {
    position: absolute;
    right: unset;
    left: 40px;
    top: 30px;
  }
}